.header {
  height: 100vh;
  width: 100vw;
  background-image: url("../images/header.jpg");
  background-size: cover;
  background-repeat: no-repeat; }
  .header .header__row {
    display: flex; }
  .header .fa-solid {
    width: 24px;
    height: 24px;
    margin: 36px 0 136px 33px;
    color: #0c474b;
    position: fixed; }
  .header .header__logo {
    width: 213px;
    height: 96px;
    margin-top: 19px;
    margin-left: 60px;
    margin-bottom: 100px; }
  .header .header__hero {
    display: flex;
    flex-direction: column;
    align-items: center; }
  .header .header__title {
    font-family: "Poppins", sans-serif;
    color: #0c474b;
    font-size: 20px;
    font-weight: 600; }
  .header .header__button {
    background-color: #0c474b;
    font-family: "Poppins", sans-serif;
    font-weight: 600;
    font-size: 16px;
    color: #ffffff;
    border-style: none;
    width: 140px;
    height: 30px;
    margin: 12px 24px 12px 24px; }
  .header .header__menu {
    font-family: "Poppins", sans-serif;
    color: #0c474b;
    display: none; }
    .header .header__menu .header__menu__ul {
      display: flex;
      flex-direction: row;
      list-style: none; }
    .header .header__menu .header__menu--li {
      padding: 0 10px 0 10px; }
  @media (min-width: 768px) {
    .header .header {
      height: 100vh;
      width: 100vw;
      margin: 0 0 465px;
      padding: 39px 323px 286px 82px;
      background-size: contain;
      background-repeat: no-repeat; }
    .header .header__logo {
      display: flex;
      justify-content: center;
      margin-top: 39px;
      margin-left: 323px;
      margin-right: 82px; }
    .header .header__hero {
      display: flex;
      flex-direction: column;
      align-items: flex-start; }
    .header .header__title {
      font-size: 26px;
      text-align: left;
      width: 330px;
      margin-left: 82px; }
    .header .header__button {
      font-size: 14px;
      width: 176px;
      height: 48px;
      margin-left: 82px;
      margin-bottom: 200px; } }
  @media (min-width: 1200px) {
    .header {
      /*
    .header__menu {
      font-size: 10px;
      display: flex;
      margin-top: 76px;
      margin-bottom: 150px;
      position: fixed;
    }*/ }
      .header .header {
        height: 100vh; }
      .header .fa-solid {
        display: none; }
      .header .header__row {
        flex-direction: row-reverse; }
      .header .header__menu--li {
        margin-left: 40px; }
      .header .header__logo {
        margin: 39px 400px 0px 93px; }
      .header .header__hero {
        margin-left: 50px; } }

.footer {
  height: 100vh;
  width: 100vw;
  background-color: #0c474b;
  font-family: "Poppins", sans-serif;
  font-size: 14px;
  color: #b6c8c9;
  display: flex;
  flex-direction: column; }
  .footer__list li {
    list-style: none; }
  .footer__box--title {
    font-family: "Poppins", sans-serif;
    font-weight: 600;
    font-size: 20px;
    margin-left: 40px;
    margin-bottom: 5px; }
  .footer__box--shop {
    margin-top: 36px;
    margin-left: 50px; }
  .footer__box--we {
    margin-left: 50px; }
  .footer__box--rrss {
    margin-top: 0;
    margin-left: 50px;
    width: 200px; }

.footer__red {
  display: flex;
  flex-direction: row; }
  .footer__red .red--contact {
    font-size: 15px;
    padding: 0 10px 0 10px; }
    .footer__red .red--contact .footer__arrow {
      display: flex;
      flex-direction: column-reverse;
      align-items: flex-end;
      margin-right: 21px; }
    .footer__red .red--contact .fa-solid fa-arrow-up {
